import Helper from '@/function.js';

import loadMore from '../../components/load-more.vue'
export default {

	data() {
		return {
			token: '',
			nodata: false,
			total: -1,
			pageIndex: 1,
			pageSize: 10,
			state: '', //状态 
			taskRank: '', //任务等级
			taskType: '', //任务类型
			taskList: [],
			menuList: [],
			menuIndex: -1,
			isdown: true,
			ismask: false,
			sortnum: 0
		};
	},
	onLoad: function(options) {
		this.taskRank = options.rank;
	},
	onShow() {

		this.token = uni.getStorageSync('token');

		this.pageIndex = 1;
		this.taskList = [];
		this.getTaskList();
	},
	onPullDownRefresh() {
		this.pageIndex = 1;
		this.taskList = [];
		this.getTaskList();
		uni.stopPullDownRefresh();
	},

	//上拉加载函数
	onReachBottom: function() {
		if (this.taskList.length < this.total)
		{
			this.pageIndex++;
			this.getTaskList();
		}
		
	},
	methods: {
		selectMenu(item) {

			for (var i = 0; i < this.menuList.length; i++) {
				this.menuList[i].isChecked = false;
			}

			item.isChecked = true;
			this.ismask = false;

			if (this.menuIndex == 1) {
				this.taskRank = item.selectstatus;
			}
			if (this.menuIndex == 0) {
				this.taskType = item.selectstatus;
			}

			this.pageIndex = 1;
			this.taskList = [];
			this.getTaskList();
		},



		closeallmask() {
			this.ismask = false;
		},

		openmask(index) {
			//console.log(index);
			if (this.menuIndex != index) {
				if (index == 0) {
					console.log('init----');
					this.menuList = [{
							name: '全部',
							isChecked: false,
							selectstatus: -1
						},
						{
							name: '抖音任务',
							isChecked: false,
							selectstatus: 0
						},


						{
							name: '快手任务',
							isChecked: false,
							selectstatus: 1
						}
					];
					this.menuIndex = 0;

					for (var i = 0; i < this.menuList.length; i++) {
						if (this.menuList[i].selectstatus == this.status) {
							this.menuList[i].isChecked = true;
							break;
						}
					}


				}

				if (index == 1) {
					this.menuList = [{
							name: '全部',
							isChecked: false,
							selectstatus: -1

						},
						{
							name: '普通任务',
							isChecked: false,
							selectstatus: 0

						},
						{
							name: '巨星任务',
							isChecked: false,
							selectstatus: 1

						}
					];
					
					this.menuIndex = 1;
					for (var i = 0; i < this.menuList.length; i++) {
						if (this.menuList[i].selectstatus == this.vehicelClass) {
							this.menuList[i].isChecked = true;
							break;
						}
					}
				}


			}


			//console.log(this.menuList);
			this.menuIndex = index;
			this.ismask = true;
		},

		/* 获得所有任务列表 */
		getTaskList() {
			var _this = this
			
			var taskRank = _this.taskRank
			if (_this.taskRank == -1)
			{
				taskRank=""
			}
			
			var taskType = _this.taskType
			if (_this.taskType == -1)
			{
				taskType=""
			}
			
			var params = {
				state: _this.state,
				taskType: taskType,
				taskRank: taskRank,
				pageIndex: _this.pageIndex,
				pageSize: _this.pageSize,
			}
			uni.request({
				url: _this.ApiGateWay + '/sale/Member/GetTaskList',
				data: params,
				method: 'POST',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {
					//console.log(res);

					if (Helper.CheckData(res)) {
						let {
							errorCode,
							errorMessage,
							list,
							total,
							totalPage
						} = res;
						_this.total = res.data.total;
						_this.taskList = _this.taskList.concat(res.data.list);
						
						//console.log(_this.taskList);
						// _this.setListStatus();
					} else {
						_this.total = true;
					}
				},
				fail: function() {
					uni.hideLoading();
				},
				complete: function() {
					uni.hideLoading();
					uni.stopPullDownRefresh()
				}


			})

		},

		/* 领取任务移除 */
		getTask(index, taskId) {
			var _this = this;
			console.log('点击移除');
			uni.request({
				url: _this.ApiGateWay + '/sale/Member/MemberGetTask?taskId=' + taskId,
				header: {
					Authorization: _this.token
				},
				method: 'POST',
				dataType: 'json',
				success: function(res) {

					if (Helper.CheckData(res)) {
						res = res.data;
						_this.taskList.splice(index, 1);
						uni.showModal({
							title: '提示',
							content: '领取成功',
							confirmColor: '#5A83E9',
							showCancel: false,
						});
					}
					else
					{
						uni.showModal({
							title: '提示',
							content: res.data.errorMessage,
							confirmColor: '#5A83E9',
							showCancel: false,
						});
					}

				},
				complete() {
					uni.hideLoading();
				},
				fail: function(res) {
					uni.stopPullDownRefresh();
				}
			});



		},

mytasorder(item) {
	console.log("==========")
	//console.log(item)
	console.log("==========")

	uni.navigateTo({
		url: '/pages/mytask/mytaskorder?item=' + JSON.stringify(item)
	})


}

	}
};
